import {Button, Card, Col, FloatingLabel, Form, Row} from 'react-bootstrap'
import {observer} from 'mobx-react-lite'
import './FloatingLabelCustom.scss'
import React, {useState} from 'react'
import {nanoid} from 'nanoid'

import {data} from "../../Data/DataCard";
import ServiceCard from "../../ServiceCard/ServiceCard";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faMagnifyingGlass} from "@fortawesome/free-solid-svg-icons/faMagnifyingGlass";
import {faXmark} from "@fortawesome/free-solid-svg-icons/faXmark";

const faPropIconFaMagnifyingGlass = faMagnifyingGlass;
const faPropIconFaXmark = faXmark;
export default observer(() => {
    const [stateVal, setStateVal] = useState('')
    const [toggleButtons, setToggleButtons] = useState(false)

    const filterVal = data.filter((val) => {
        return val.name.toLowerCase().includes(stateVal.toLowerCase())
    })

    const onBlur = () => setToggleButtons(false)

    const onFocus = () => {
        setToggleButtons(true)
    }


    return (
        <div>
            <Form autoComplete={'off'} onSubmit={(e) => e.preventDefault()}>
                <Card className={'floating-label-custom-card'}>
                    <Row>

                        <Col style={{display: 'inline-block'}} xs={9} sm={9} md={10} lg={11}>

                            <Form.Control
                                onFocus={onFocus}
                                onBlur={onBlur}
                                value={stateVal}
                                onChange={(e) => setStateVal(e.target.value)}
                                maxLength={100}
                                placeholder="Поиск МФО"
                                className={'floating-label-custom-card-form'}
                            />


                        </Col>
                        <Col
                            xs={3}
                            sm={3}
                            md={2}
                            lg={1}
                            className={'floating-form-btn'}
                        >

                            {
                                !toggleButtons ?
                                    <Button
                                        className={'floating-btn-clear'}
                                        onClick={
                                            () => {
                                                setStateVal('')
                                            }
                                        }
                                        title={'Очистить'}
                                        variant={'outline-light'}
                                    >
                                        <FontAwesomeIcon className={'floating-label-custom-icon'}
                                                         icon={faPropIconFaMagnifyingGlass}/>
                                    </Button> :
                                    stateVal.length > 0 ?
                                        <Button
                                            className={'floating-btn-clear'}
                                            onClick={
                                                () => {
                                                    setStateVal('')
                                                }
                                            }
                                            title={'Очистить'}
                                            variant={'outline-light'}
                                        >
                                            <FontAwesomeIcon className={'bottom-panel-icon'} icon={faPropIconFaXmark}/>
                                        </Button>

                                        :
                                        null


                            }
                        </Col>
                    </Row>
                </Card>
            </Form>

           <div>
               {filterVal.map((data) => (
                   <Col key={nanoid()} md={12}  style={{paddingBottom: ".7rem"}}>
                       <ServiceCard data={data}/>
                   </Col>
               ))}
           </div>

        </div>
    )
})
