import {Button, Card, Col, FloatingLabel, Form, Row} from 'react-bootstrap'
import {observer} from 'mobx-react-lite'
import './FloatingLabelCustomNum.scss'
import React from 'react'

import {faMagnifyingGlass} from "@fortawesome/free-solid-svg-icons/faMagnifyingGlass";
import {faXmark} from "@fortawesome/free-solid-svg-icons/faXmark";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";

const faPropIconFaMagnifyingGlass = faMagnifyingGlass;
const faPropIconFaXmark = faXmark;

interface IFloatingLabelCustom {
    value: any
    onChange?: React.ReactEventHandler
    onclickBtn?: React.ReactEventHandler
    onclickBtnSecond?: React.ReactEventHandler
    label: string
    type?: string
}

export default observer(({
                             value,
                             onChange,
                             onclickBtn,
                             onclickBtnSecond,
                             label,
                             type
                         }: IFloatingLabelCustom) => {

console.log(value)
    return (

        <Form autoComplete={'off'} onSubmit={(e) => e.preventDefault()}>
            <Card className={'floating-label-Custom-card'}>
                <Row>
                    <Col
                        style={{display: 'inline-block',}}
                        xs={8}
                        sm={8}
                        md={10}
                    >
                        <FloatingLabel
                            controlId="floatingInput"
                            label={label}
                            className="mb-3"
                        >
                            <Form.Control value={value} onChange={onChange} maxLength={100} type={type}
                                          placeholder="Поиск"/>
                        </FloatingLabel>
                    </Col>
                    <Col
                        xs={4}
                        sm={4}
                        md={2}
                        className={'floating-form-btn'}

                    >
                        {
                            value?
                                <Button style={{margin: '0 5px'}} onClick={onclickBtnSecond} title={'Очистить'}
                                        variant={"outline-light"}>
                                    <FontAwesomeIcon icon={faPropIconFaXmark} style={{fontSize: '1.2rem'}}/>
                                </Button>
                                :
                                null
                        }
                        <Button type={'submit'} style={{textAlign: 'center'}} onClick={onclickBtn} title={'Поиск'}
                                variant={"outline-light"}>
                            <FontAwesomeIcon icon={faPropIconFaMagnifyingGlass} style={{fontSize: '1.2rem'}}/>
                        </Button>
                    </Col>


                </Row>
            </Card>

        </Form>


    )
})
